import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './plugins/vantUI'
import './filters'
import './api/index'
import Vant from 'vant'
import VueI18n from 'vue-i18n'
import htmlToPdf from './print/htmlToPdf'
import { VueJsonp } from 'vue-jsonp'
Vue.use(VueJsonp)
Vue.use(VueI18n)
Vue.use(htmlToPdf)
const i18n = new VueI18n({
  locale: localStorage.getItem('languageSet') || 'zh', // ‰ªélocalStorageÈáåËé∑ÂèñÁî®Êà∑‰∏≠Ëã±ÊñáÈÄâÊã©ÔºåÊ≤°ÊúâÂàôÈªòËÆ§‰∏≠Êñá
  messages: {
    zh: require('./components/language/zh'),
    en: require('./components/language/en'),
    jp: require('./components/language/jp')
  }
})
Vue.config.productionTip = false

// document.body.addEventListener('touchmove', eve => {
//   eve.preventDefault()
//   eve.stopPropagation()
// }, { passive: false })

router.beforeEach((to, form, next) => {
  // Â¶ÇÊûúËøõÂÖ•Âà∞ÁöÑË∑ØÁî±ÊòØÁôªÂΩïÈ°µÊàñËÄÖÊ≥®ÂÜåÈ°µÈù¢ÔºåÂàôÊ≠£Â∏∏Â±ïÁ§∫
  // Èô§‰∫ÜËøõÂÖ•ÁôªÂΩïÈ°µÈù¢Â§ñ ÊâÄÊúâÁöÑÈ°µÈù¢ÈÉΩÂøÖÈ°ªÊúâtokenÂ¶ÇÊûúÊ≤°ÊúâÂàô‰∏çÈúÄ‰ªñËÆøÈóÆ
  if (to.path === '/login') {
    if (localStorage.getItem('token')) {
      // Â≠óÁ¨¶‰∏≤ false true Âõ†‰∏∫localStorageÈáåÈù¢ÊòØÂ≠óÁ¨¶‰∏≤
      if (localStorage.getItem('actived') === 'false') {
        router.push('/wizardpage/' + localStorage.getItem('usenameID'))
        return
      }
      router.push(`/home/${localStorage.getItem('usenameID')}`)
    }
    next()
  } else if (to.path === '/wipedata' || to.path === '/gcmsshezhi') {
    next()
  } else if (!localStorage.getItem('token')) {
    Vant.Toast({
      message: localStorage.getItem('languageSet') === 'zh' || !localStorage.getItem('languageSet') ? 'Â∞öÊú™ÁôªÂΩïÔºåËØ∑ÂÖàÁôªÂΩï' : 'Please login first',
      // message: Vue.$t('tsytongyong.qxdenglu'),
      position: 'top'
    })
    next('/login')
  } else if (localStorage.getItem('token')) {
    // console.log(111)
    next()
    if (localStorage.getItem('actived') === 'false') {
      router.push('/wizardpage/' + localStorage.getItem('usenameID'))
    }
  } else {
    next()
  }
})
new Vue({
  router,
  i18n,
  render: h => h(App)
}).$mount('#app')
