// ÂØºÂá∫È°µÈù¢‰∏∫PDFÊ†ºÂºè
import html2Canvas from 'html2canvas'
import JsPDF from 'jspdf'
export default {
  install(Vue, options) {
    Vue.prototype.getPdf = function(title) {
      var element = document.querySelector('#pdfDom') // Ëøô‰∏™domÂÖÉÁ¥†ÊòØË¶ÅÂØºÂá∫pdfÁöÑdivÂÆπÂô®
      setTimeout(() => {
        html2Canvas(element).then(function(canvas) {
          var contentWidth = canvas.width
          var contentHeight = canvas.height

          // ‰∏ÄÈ°µpdfÊòæÁ§∫htmlÈ°µÈù¢ÁîüÊàêÁöÑcanvasÈ´òÂ∫¶
          var pageHeight = contentWidth / 592.28 * 841.89
          // Êú™ÁîüÊàêpdfÁöÑhtmlÈ°µÈù¢È´òÂ∫¶
          var leftHeight = contentHeight
          // È°µÈù¢ÂÅèÁßª
          var position = 0
          // a4Á∫∏ÁöÑÂ∞∫ÂØ∏[595.28,841.89]ÔºåhtmlÈ°µÈù¢ÁîüÊàêÁöÑcanvasÂú®pdf‰∏≠ÂõæÁâáÁöÑÂÆΩÈ´ò
          var imgWidth = 595.28
          var imgHeight = 595.28 / contentWidth * contentHeight

          var pageData = canvas.toDataURL('image/jpeg', 1.0)

          var pdf = new JsPDF('', 'pt', 'a4')

          // Êúâ‰∏§‰∏™È´òÂ∫¶ÈúÄË¶ÅÂå∫ÂàÜÔºå‰∏Ä‰∏™ÊòØhtmlÈ°µÈù¢ÁöÑÂÆûÈôÖÈ´òÂ∫¶ÔºåÂíåÁîüÊàêpdfÁöÑÈ°µÈù¢È´òÂ∫¶(841.89)
          // ÂΩìÂÜÖÂÆπÊú™Ë∂ÖËøápdf‰∏ÄÈ°µÊòæÁ§∫ÁöÑËåÉÂõ¥ÔºåÊó†ÈúÄÂàÜÈ°µ
          if (leftHeight < pageHeight) {
            pdf.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight)
          } else {
            while (leftHeight > 0) {
              pdf.addImage(pageData, 'JPEG', 0, position, imgWidth, imgHeight)
              leftHeight -= pageHeight
              position -= 841.89
              // ÈÅøÂÖçÊ∑ªÂä†Á©∫ÁôΩÈ°µ
              if (leftHeight > 0) {
                pdf.addPage()
              }
            }
          }
          pdf.save('report:' + localStorage.getItem('pdfrenwuming') + '.pdf')
        })
      }, 0)
    }
  }
}
