        </div>
        <div class="conttop">
            <div>{{$t('daojishiyemian.renwumingcheng')}}Ôºö{{taskname}}</div>
                 <h3>{{$t('daojishiyemian.lirenwukshy')}}</h3>
        <h4><img src="../../assets/Countdown.gif" alt="">
        <span>{{msg}}</span></h4>
        <h5>{{$t('daojishiyemian.qingrenyuanlk')}}</h5>
        </div>
           <div class="bottomgaid">
        <div class="bottomgaidson"  @click="abotask">
            <img src="../../assets/tongyongstop.png" alt="">
            <div>{{$t('homes.zhongzhi')}}</div>
        </div>
    </div>
    <Absoluts ref="absoluts"  />
    </div>
