<script>
import Absoluts from '../../components/tutorabsoluts'
export default {
  data: () => ({
    msg: '',
    taskname: '',
    timer: null,
    showtime: false
  }),
  inject: ['reload'],
  created() {
    // ËÆæÁΩÆÂÆöÊó∂Âô® ÊØè‰∏ÄÁßíÊâßË°å‰∏ÄÊ¨° Âà∑Êñ∞Áä∂ÊÄÅ
    this.timer = setInterval(async() => {
    // Ëé∑ÂèñÁé∞Âú®ÁöÑÁä∂ÊÄÅ‰∏ÄÁõ¥Âà∑Êñ∞ Â¶ÇÊûú‰∏çÊòØÊ≠£Âú®ÂÄíËÆ°Êó∂ÂíåÊÅ¢Â§çÂÄíËÆ°Êó∂Â∞±Á´ãÂç≥Ë∑≥ËΩ¨Âà∞Ê∂àÊùÄÈ°µÈù¢
      try {
        const params = {
          cmd: 'get_task_state',
          include_points: false
        }
        // ÂêéÂè∞ËøîÂõûÁöÑÁä∂ÊÄÅÁ†Å Ë∑≥ËΩ¨Ê∂àÊùÄÈ°µÈù¢Âπ∂‰∏îÊ∏ÖÈô§ÂÆöÊó∂Âô®
        const res = await this.Api.getotsstate(params)
        this.taskname = res.data.task_name
        if (res.data.status === 'START_COUNT_DOWN' || res.data.status === 'RESUME_COUNT_DOWN') {
          this.msg = res.data.count_down_state.remain
          // ÂÄíËÆ°Êó∂
        } else {
          const id = localStorage.getItem('usenameID')
          this.$router.replace(`/disntask/${id}`)
          clearInterval(this.timer)
          this.timer = null
        }
      } catch (err) {
        // Â¶ÇÊûúÂêéÂè∞Êé•ÂèóÁöÑÁä∂ÊÄÅÁ†Å‰∏∫404ËØ¥ÊòéÊ≠§Êó∂Ê≤°ÊúâÊ∂àÊùÄ‰ªªÂä°Â∞±Ë¶ÅË∑≥ÂõûÊ∂àÊùÄËØ¶ÊÉÖÈ°µÈù¢
        if (err.request.status === 404) {
          clearInterval(this.timer)
          this.timer = null
          const id = localStorage.getItem('usenameID')
          this.$router.replace(`/disntask/${id}`)
        }
      }
    }, 1000)
  },
  methods: {
  // ÂèëÈÄÅÁªàÊ≠¢Ê∂àÊùÄËØ∑Ê±Ç
    fn3() {
      this.$refs.absoluts.$el.style.display = 'block'
    },
    abotask() {
      // try {
      //   const abortask = {
      //     cmd: 'abort_task'
      //   }
      //   const resabort = this.Api.getotsstate(abortask)
      //   console.log(resabort)
      // } catch {
      // }
      this.$Dialog.confirm({
        title: this.$t('tsytongyong.xiaoshazhongzhi'),
        message: this.$t('tsytongyong.shifouzhongzhixs'),
        confirmButtonText: this.$t('xiaosharw.queren'),
        cancelButtonText: this.$t('xiaosharw.quxiao')
        // Â¶ÇÊûúÁ°ÆËÆ§ÈÄÄÂá∫‰ª•ÂêéÂèëÈÄÅËØ∑Ê±ÇÂà†Èô§ÂêéÂè∞TokenÂπ∂‰∏îÊú¨Âú∞Token‰πüÂà†Èô§
        // Âª∂Êó∂500ÊØ´Áßí
      }).then(() => {
        const abortask = {
          cmd: 'abort_task'
        }
        this.Api.getotsstate(abortask).then((res) => {
          console.log(res)
        })
        // this.reload()
      })
    }
  },
  components: {
    Absoluts
  },
  beforeDestroy() {
    clearInterval(this.timer)
    this.timer = null
  }
}
</script>
